@media only screen and (max-width: 800px) {
  .todo-items {
    height: 70vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.title-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 10vh;
}

.remove-all {
  display: flex;
  justify-content: flex-end;
}

.todo-input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 20vh;
}

.add {
  margin-top: 10px;
  width: 245px;
}

.todo-items {
  height: 70vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.item {
  display: flex;
  flex-direction: column;
  border-style: solid;
  margin: 15px;
  height: 200px;
  width: 200px;
}

.delete {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  margin-top: auto;
  height: 35%;
}

.text {
  height: 10%;
}

.text {
  margin: auto;
}

.edit-text {
  height: 100%;
}

input {
  height: 10%;
  margin: 5px;
}

.task-table {
  width: 50%;
  margin: auto;
}

.todo-cell {
  width: 60%;
}

.status {
  display: flex;
  justify-content: center;
}
